export class StateHolder {
    constructor() {
        this.statesData = new Map();
        this.presentationStates = [];
    }
    static getInstance() {
        if (!StateHolder.instance) {
            StateHolder.instance = new StateHolder();
        }
        return StateHolder.instance;
    }
    setState(id, state) {
        this.statesData.set(id, state);
    }
    getState(id) {
        return this.statesData.get(id);
    }
    setPresentationStates(state) {
        this.presentationStates.push(state);
    }
    getPresentationStates() {
        return this.presentationStates;
    }
}
