import { Sound } from "@pixi/sound";
import { Assets } from "pixi.js";
import { PreloaderEvents } from "../eventsManager/PreloaderEvents";
import { Globals } from "../Globals";
import { EventsManager } from "../eventsManager/EventsManager";
import { DeviceManager } from "../managers/events/deviceManager";
import eventBus from "../events/EventBus";
import { GameStatus } from "./GameStatus";
export class Preloader {
    constructor() {
        this.assetsName = "game-assets";
        this.manifestName = "./assets-manifest.json";
        this.gameFolder = "";
        this.device = DeviceManager.getInstance().isMobile() ? "mobile" : "desktop";
        this.loadManifest(this.updateProgress);
    }
    async loadManifest(onProgress) {
        /*const response = await fetch(`./assets/settings.json`);
        if (!response.ok) {
          throw new Error(`Failed to fetch settings.json: ${response.statusText}`);
        }*/
        //const settings = await response.json();
        console.log("LOAD MANIFEST....");
        const basePath = window.location.pathname.replace(/\/[^\/]*$/, "/") + "assets/";
        try {
            const manifestResponse = await fetch(this.manifestName);
            if (!manifestResponse.ok)
                throw new Error(`Failed to load manifest: ${manifestResponse.status}`);
            const manifest = await manifestResponse.json();
            console.log("MANIFEST: ", manifest);
            const loadDataHolder = [];
            const soundAssets = [];
            let totalSoundAssets = 0;
            // Prepare assets and count sound assets
            manifest.bundles.forEach((bundle) => {
                bundle.assets.forEach((asset) => {
                    asset.src = basePath + asset.src;
                    Assets.add(asset);
                    loadDataHolder.push(asset.id);
                    if (asset.src.endsWith(".mp3") ||
                        asset.src.endsWith(".ogg") ||
                        asset.src.endsWith(".wav")) {
                        asset.type = "audio";
                        totalSoundAssets++;
                    }
                });
            });
            console.log("Loading.................................");
            // Load textures (80% progress)
            let texturesData;
            try {
                texturesData = await Assets.load(loadDataHolder, (val) => {
                    this.updateProgress(val * 0.8);
                });
                // success: texturesData is ready
            }
            catch (error) {
                console.error("Error while loading assets:", error);
                // You can also display an error message or trigger a fallback here
            }
            Globals.assetsLibrary = texturesData;
            console.log("Textures Loaded:", texturesData);
            console.log(" Game Assets Loaded:");
            // Load sounds (remaining 20%)
            let loadedSoundAssets = 0;
            const soundPromises = [];
            manifest.bundles.forEach((bundle) => {
                bundle.assets.forEach((asset) => {
                    if (asset.src.endsWith(".mp3") ||
                        asset.src.endsWith(".ogg") ||
                        asset.src.endsWith(".wav")) {
                        const soundPromise = new Promise((resolve) => {
                            const sound = Sound.from(asset.src);
                            soundAssets.push({ name: asset.id, sound });
                            setTimeout(() => {
                                loadedSoundAssets++;
                                const progress = Math.min(1, 0.8 + (loadedSoundAssets / totalSoundAssets) * 0.2);
                                onProgress(progress);
                                //console.log(` Sound Loaded: ${asset.name}`);
                                resolve();
                            }, 20);
                        });
                        soundPromises.push(soundPromise);
                    }
                });
            });
            await Promise.all(soundPromises);
            Globals.SoundAssets = soundAssets;
            console.log(" All Sounds Loaded:", soundAssets);
            EventsManager.getInstance().dispatchEvent(new PreloaderEvents(PreloaderEvents.LOADING_COMPLETE));
            return { soundAssets };
        }
        catch (e) {
            //console.error(" Error loading manifest or assets", e);
        }
    }
    updateProgress(progress) {
        //console.log(progress);
        eventBus.emit(GameStatus.LOADING, { value: progress });
    }
}
