import { BaseGameScene } from "./BaseGameScene";
import { Graphics, Text } from "pixi.js";
import eventBus from "../events/EventBus";
export class StartScreen extends BaseGameScene {
    constructor(sceneName, container) {
        super(sceneName, container);
        // Create button background
        this.startButton = new Graphics();
        this.startButton.beginFill(0x0077ff);
        this.startButton.drawRoundedRect(0, 0, 300, 80, 10);
        this.startButton.endFill();
        // Center the button
        this.startButton.x = 1920 / 2 - 150;
        this.startButton.y = 1080 / 2 - 40;
        // Make interactive
        this.startButton.interactive = true; // Must be interactive first
        this.startButton.cursor = "pointer";
        // Hover effect
        this.startButton.on("pointerover", () => {
            this.startButton.tint = 0x00aaff;
        });
        this.startButton.on("pointerout", () => {
            this.startButton.tint = 0xffffff;
        });
        // Click event
        this.startButton.on("pointerdown", () => {
            this.onStartClick();
        });
        this.addChild(this.startButton);
        // Text on button
        this.startText = new Text("START", { fill: 0xffffff, fontSize: 36, fontWeight: "bold" });
        this.startText.anchor.set(0.5);
        this.startText.x = this.startButton.x + 150;
        this.startText.y = this.startButton.y + 40;
        this.addChild(this.startText);
    }
    onShow() {
        console.log("StartScreen is active");
    }
    onHide() {
        console.log("StartScreen hidden");
    }
    onStartClick() {
        console.log("Start button clicked!");
        // Dispatch an event, or call ScreenManager to switch to GameScreen
        eventBus.emit("START_GAME");
    }
}
